@import "../_component.scss";

.business {
	.program-wrap{
		padding: 30px;
		border-top: 1px solid #000;
		border-bottom: 1px solid #ccc;
		background-color: #fff;
	}
	.table-horizontal {
		.th {
			width: 233px;
			time {
				color: #888;
				font-weight: 400;
				display: block;
			}
		}
		.same-form {
			background: {
				color: #fcfcfc;
			}
		}
	}

	.same-data ~ .multi-list{
		margin-top: 12px;
	}

	.add-time-data {
		li {
			margin: {
				top: 20px;
			}
		}
		time {
			color: #717171;
			font-size: 14px;
		}
		.title {
			display: block;
			margin: {
				bottom: 8px;
			}
		}
		.btn {
			line-height: 0;
			width: 26px;
			height: 26px;
			padding: 0;
		}
	}

	.program-content {
		color: #888;
		margin-bottom: 100px;
		padding: 46px 30px;
		border-bottom: 1px solid #ccc;
	}

	.board-detail{
		border-top: 1px solid #000;
		.detail-summary{
			padding-top: 35px;
			padding-bottom: 35px;
		}
	}

}

.multi-list{
	.btn {
		padding: {
			right: 20px;
			left: 20px;
		};
	}
}
.outreach {
	margin-top: 100px;
	padding: 70px 0;
	background-color: #f5f5f5;
	.inner {
		max-width: 880px;
		margin: 0 auto;
	}
	h3 {
		margin-bottom: 15px;
		font-weight: 700;
		font-size: 24px;
		color: #333;
	}
	.result {
		@include clear;
		position: relative;
		margin-top: 50px;
		text-align: center;
		white-space: nowrap;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 20%;
			right: 20%;
			top: 50%;
			border: 2px dashed #ccc;
		}
		li {
			position: relative;
			z-index: 1;
			display: inline-block;
			width: 246px;
			height: 246px;
			margin-left: 70px;
			padding: 40px 0;
			border-radius: 50%;
			box-sizing: border-box;
			border-width: 2px;
			border-style: solid;
			text-align: center;
			font-size: 22px;
			background-color: #fff;
			&:first-child {
				margin-left: 0;
			}
			&.volunteer {
				color: #0090da;
				border-color: #0090da;
			}
			&.volunteer-time {
				padding-top: 35px;
				color: #fff;
				background-color: #0090da;
				border-color: #0090da;
			}
			&.beneficiary {
				color: #b4d76e;
				border-color: #b4d76e;
			}
			.icon {
				display: block;
				margin: 0 auto 20px;
			}
			strong {
				display: block;
				font-size: 35px;
				span {
					font-size: 42px;
				}
			}
		}
	}
}

.business-wrap {
	.section {
		> .program {
			margin-top: 100px;
		}
	}

	.slogan {
		@include clear;
		display: table;
		margin-top: 40px;
		padding: 40px 0;
		border: {
			top: 1px solid #000;
			bottom: 1px solid #e8e8e8;
		};
		.keyword{
			display: table-cell;
			width: 25%;
			font-weight: 700;
			font-size: 24px;
			text-align: center;
			box-sizing: border-box;
		}
		.mean{
			display: table-cell;
			padding-left: 30px;
			font-size: 16px;
			letter-spacing: -.5px;
			vertical-align: top;
		}
	}

	.submain-desc{
		position: relative;
		margin-top: 70px;
		margin-bottom: 100px;
		padding-top: 60px;
		&.bg-circle{
			&:before{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				display: block;
				width: 100%;
				height: 458px;
				background-position: 50% 0;
				background-repeat: no-repeat;
			}
			&-blue:before{
				background-image: url('../images/bg-circle-blue.png');
			}
			&-green:before{
				background-image: url('../images/bg-circle-green.png');
			}
		}
		h3{
			position: relative;
			padding-bottom: 25px;
			font-weight: 300;
			text-align: center;
			font-size: 42px;
			letter-spacing: -.5px;
			&:after{
				content: '';
				position: absolute;
				bottom: 0;
				left: 50%;
				display: inline-block;
				width: 41px;
				height: 2px;
				margin-left: -20px;
				background-color: #000;
			}
			strong{
				display: block;
				padding-bottom: 5px;
			}
		}
		p {
			margin-top: 35px;
			line-height: 1.625;
			color: #888;
		}
		h4{
			margin-top: 50px;
			font-weight: 700;
			font-size: 18px;
			color: #333;
		}
		&-more{
			p{
				margin-top: 15px;
			}
		}
		.btn {
			position: relative;
			z-index: 1;
		}
	}
	.headline {
		text-align: center;
		h3 {
			font-size: 44px;
			font-weight: bold;
		}
		.description {
			font-weight: 400;
			color: #888;
			line-height: 1.5;
			letter-spacing: -.5px;
			p {
				margin-top: 50px;
			}
			span {
				font-weight: bold;
			}
			&.border {
				padding-bottom: 60px;
				border-bottom: 1px solid #ccc;
			}
		}
	}

	h4 {
		&.bullet {
			position: relative;
			color: #007abc;
			font-size: 24px;
			padding: {
				bottom: 10px;
				left: 16px;
				top: 0;
			}
			&:before {
				content: '';
				position: absolute;
				top: 11px;
				left: 0;
				width: 11px;
				height: 11px;
				border: 3px solid #007abc;
				border-radius: 50%;
				box-sizing: border-box;
			}
			small{
				font-size: 24px;
				font-weight: 400;
			}
		}
	}
	.con {
		&.indent {
			margin-left: 20px;
		}
	}
	.cards {
		padding: 70px 0 105px;
		background-color: #f5f5f5;
		> .inner {
			position: relative;
			max-width: 880px;
			margin: 0 auto;
		}
		&.finance {
			a {
				&:hover {
					background-color: #def4ff;
				}
			}
		}
		&.community {
			a {
				&:hover {
					background-color: #edf5dc;
				}
			}
		}
		h4 {
			margin-bottom: 20px;
			color: #333;
		}
		ul {
			@include clear;
			margin: {
				left: -7.5px;
				right: -7.5px;
			}
			//overflow: hidden;
			li {
				float: left;
				width: 20%; /* 2022-03-18 25% -> 20% */
				padding: 7.5px;
				box-sizing: border-box;
				a {
					display: block;
					width: 100%;
					height: 220px;
					white-space: nowrap;
					text-align: center;
					background-color: #fff;
					box-shadow: 10px 10px 10px rgba(0, 0, 0, .06);
					transition: background .4s;
					&:after {
						content: '';
						display: inline-block;
						height: 100%;
						vertical-align: middle;
					}
					div {
						display: inline-block;
						vertical-align: middle;
					}
					.icon {
						display: block;
						margin: 0 auto 20px;
					}
					.icon-business-financial-2{
						margin-left: 10px;
					}
					.icon2 {
						display: block;
						margin: 0 auto 10px;
					}
					.icon3 {
						display: block;
						margin: 0 auto 20px;
					}

					span {
						display: inline-block;
						height: 42px;
						white-space: normal;
						&.line1 {
							line-height:42px;
						}
					}
				}
			}
		}
	}
	.education {
		> .inner {
			position: relative;
			max-width: 880px;
			margin: 0 auto;
		}
		.grade {
			@include clear;
			margin: {
				top: -20px;
				left: 30px;
				right: 30px;
				bottom: 60px;
			}
			li {
				float: left;
				width: 16.666%;
				font-size: 18px;
				text-align: center;
				strong {
					display: block;
					margin-top: 10px;
					font-size: 20px;
				}
			}
		}
		.lesson {
			margin-bottom: 50px;
			.con {
				padding: 15px 10px 0;
				font-size: 16px;
			}
			strong {
				display: block;
				margin-bottom: 10px;
				font-size: 20px;
			}
			figure {
				margin: {
					left: 44px;
					right: 44px;
				}
			}
			video{
				width: 100%;
			}
		}
		.course {
			border-top: 1px solid #ebebeb;
			padding-top: 50px;
			> li {
				display: flex;
				align-items: center;
				margin-bottom: 30px;
			}
			&-title {
				display: flex;
				font-size: 20px;
				align-items: center;
				text-align: center;
				justify-content: center;
				width: 148px;
				height: 148px;
				box-sizing: border-box;
				letter-spacing: -0.75px;
				border: {
					radius: 50%;
					width: 8px;
					style: solid;
				}
				margin: 0 20px;
			}
			.visit {
				.course-title {
					color: #1f8bcc;
					border-color: rgba(97, 173, 218, .7);
				}
				.depth {
					li {
						&:before {
							background-color: #0090da;
						}
					}
				}
			}
			.workshop {
				.course-title {
					color: #94ac35;
					border-color: rgba(167, 193, 63, .7);
					padding-top: 8px;
				}
				.depth {
					li {
						&:before {
							background-color: #a7c13f;
						}
					}
				}
			}
			.depth {
				margin-left: 40px;
				width: 630px;
				> li {
					width: 50%;
					float: left;
					margin-bottom: 16px;
					position: relative;
					&:before {
						content: '';
						position: absolute;
						top: 7px;
						left: -10px;
						width: 6px;
						height: 6px;
					}
					&:last-child {
						margin-bottom: 0;
					}
				}
				&.even {
					li {
						&:nth-last-child(2), &:last-child {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
	.finance-submain-list{
		padding-bottom:70px;
		.spend {
			.course-title {
				color: #1f8bcc;
				border-color: rgba(97, 173, 218, .7);
			}
			.depth {
				li {
					&:before {
						background-color: #0090da;
					}
				}
			}
		}
		.save {
			.course-title {
				color: #94ac35;
				border-color: rgba(193, 211, 120, .7);
			}
			.depth {
				li {
					&:before {
						background-color: #94ac35;
					}
				}
			}
		}
		.borrow {
			.course-title {
				color: #3a9861;
				border-color: rgba(145, 197, 167, .7);
			}
			.depth {
				li {
					&:before {
						background-color: #3a9861;
					}
				}
			}
		}
		.plan {
			.course-title {
				color: #1774ae;
				border-color: rgba(139, 185, 214, .7);
			}
			.depth {
				li {
					&:before {
						background-color: #1774ae;
					}
				}
			}
		}
		.list-txt-info{
			text-align:right;
			margin-bottom:50px;
			color:#888;
		}
	}
	.information {
		.inner {
			max-width: 880px;
			margin: 0 auto;
		}
		h4 {
			margin: 0 0 30px 0;
			padding: 0;
			color: #333;
			font-size: 18px;
		}
		.notice {
			color: #333;

			> ul {
				> li {
					strong{
						position: relative;
						display: inline-block;
						width: 230px;
						padding-left: 23px;
						font-weight: 700;
						box-sizing: border-box;
						vertical-align: top;
					}
					.flag{
						position: absolute;
						top: 3px;
						left: 0;
						display: inline-block;
						border: 8px solid transparent;
						&-1{
							border-color: #1e8bcc transparent transparent #1e8bcc;
						}
						&-2{
							border-color: #48afde transparent transparent #48afde;
						}
						&-3{
							border-color: #a7c13f transparent transparent #a7c13f;
						}
					}
					ul {
						display: inline-block;
						vertical-align: top;
						&.list-inline {
							max-width: 70%;
							li {
								display: inline-block;
								min-width: 100px;
								padding: 3px 30px 0 0;
							}
						}
						li {
							// text-transform: uppercase;
							&:before {
								content: '  ';
							}
						}
					}
				}
			}
		}
		.plan {
			@include clear;
			position: relative;
			padding-top: 45px;
			color: #888;
			> span {
				display: block;
				position: absolute;
				left: 0;
				top: 95px;
				width: 94px;
				height: 94px;
				z-index: 2;
				padding: 0;
				line-height: 94px;
				text-align: center;
				color: #fff;
				background-color: #48afde;
				border-radius: 50%;
			}
			ul {
				@include clear;
				position: relative;
				margin-top: 75px;
				margin-left: 60px;
				&:before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					border: 2px solid #48afde;
					border-left: none;
					border-radius: 0 65px 65px 0;
					width: 100%;
					height: 130px;

				}
			}
			li {
				position: relative;
				float: left;
				width: 20%;
				height: 138px;
				margin-top: -6px;
				padding-top: 25px;
				z-index: 1;
				box-sizing: border-box;
				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					top: 0;
					width: 14px;
					height: 14px;
					border: 2px solid #48afde;
					background-color: #fff;
					box-sizing: border-box;
					border-radius: 50%;
				}
				&:first-child {
					margin-left: 15%;
				}
				&:nth-child(4n+1) {
					clear: left;
				}
				&:nth-child(4) ~ li {
					clear: left;
					float: right;
					width: 33%;
					&:last-child {
						float: left;
					}
				}
				&.major {
					&:before {
						background-image: repeating-linear-gradient(-45deg, transparent, transparent 1px, rgba(0,170,165,1) 2px, rgba(0,170,165,1) 3px);
					}
				}
			}
			strong {
				display: block;
				color: #48afde;
			}
		}
		.wrap-video{
			video{
				width: 100%;
			}
		}
	}
	.inclusion-program{
		margin-bottom: 64px;
		border-top: 1px dashed #d9d9d9;
	}
	.inclusion-box{
		border-bottom: 1px dashed #d9d9d9;
		padding: 40px 0;
		@include clear;
		.flag{
			position: absolute;
			top: 3px;
			left: 0;
			display: inline-block;
			border: 8px solid;
		}
		.inclusion-title{
			position: relative;
			float: left;
			width: 250px;
			padding-left: 23px;
			vertical-align: top;
			box-sizing: border-box;
			em{
				font-size: 18px;
				font-weight: 700;
			}
			small{
				display: block;
				font-size: 16px;
				font-weight: 400;
			}
		}
		.inclusion-desc{
			float: left;
			width: calc(100% - 250px);
			&-box{
				float: left;
				width: 50%;
				& > strong{
					display: block;
					margin-bottom: 22px;
					font-size: 16px;
					font-weight: 700;
				}
				&:nth-child(3){
					width:100%;
					margin-top:22px;
				}
			}
		}
		.inclusion-program-box{
			margin-bottom: 22px;
			&:last-child{
				margin-bottom: 0;
			}
			& > strong{
				position: relative;
				padding-left: 10px;
				font-size: 16px;
				color: #333;
				&:before{
					content: '';
					position: absolute;
					top: 8px;
					left: 0;
					display: inline-block;
					width: 3px;
					height: 3px;
				}
			}
			ul{
				margin-top: 5px;
			}
			li{
				position: relative;
				padding-left: 10px;
				padding-right: 10px;
				color: #999;
				font-size: 16px;
				line-height: 1.75;
				letter-spacing: -1px;
				&:before{
					content: '-';
					position: absolute;
					top: 0;
					left: 0;
					display: inline-block;
				}

			}
		}
		&-1{
			.flag{
				border-color: #48afde transparent transparent #48afde;
			}
			.inclusion-title{
				color: #48afde;
			}
			.inclusion-desc{
				&-box{
					& > strong{
						color: #48afde;
					}
				}
			}
			.inclusion-program-box{
				& > strong{
					&:before{
						background-color: #48afde;
					}
				}
			}
		}
		&-2{
			border-bottom:0 none;
			.flag{
				border-color: #a7c13f transparent transparent #a7c13f;
			}
			.inclusion-title{
				color: #a7c13f;
			}
			.inclusion-program-box{
				& > strong{
					&:before{
						background-color: #a7c13f;
					}
				}
			}
		}
	}

	.box-wrap-information{
		background-color: #f7f7f7;
		margin-top: 70px;
		.section-contents{
			border:0;
			padding:70px 0;
			width:100%;
			max-width:880px;
			margin:0 auto;
			.box-top
			{
				& > strong{
					padding-left: 17px;
					position: relative;
					display: block;
					color: #007abc;
					font-size: 24px;
					line-height: 1;
					&:before {
						display: block;
						content: "";
						width: 12px;
						height: 12px;
						position: absolute;
						top: 50%;
						left: 0;
						margin-top: -6px;
						background-color: #007abc;
						border-radius: 100px;
					}
					&:after {
						display: block;
						content: "";
						width: 6px;
						height: 6px;
						position: absolute;
						top: 50%;
						left: 3px;
						margin-top: -3px;
						background-color: #fff;
						border-radius: 100px;
					}
				}
				.text {
					margin-top: 25px;
					font-size: 16px;
					line-height: 1.5;
				}
			}
			.box-cont {
				margin-top: 60px;
				& > div{
					position: relative;
					overflow: hidden;
					& > strong {
						font-size: 18px;
						float: left;
						line-height:40px;
						:after{
							display:none;
						}
					}
					& > a.btn{
						float: right;
					}
					& > .btn-info-pc{
						display:inline-flex;
					}
				}
				& > .box-info-pc{
					display:block;
				}
				& > .box-info-mo{
					display:none;
				}
				ul{
					&:after {
						content: '';
						display: block;
						clear: both;
					}
					li {
						float: left;
						width: 429px;
						height: 221px;
						background-color: #fff;
						margin-left: 20px;
						margin-top: 20px;
						border-bottom: 1px solid #ccc;
						border-right: 1px solid #ccc;
						&:nth-of-type(2n+1){
							margin-left: 0px;
						}
						& > i{
							display:table-cell;
							padding:40px 0 0 40px;
							vertical-align: middle;
						}
						& > .texts-box{
							display:table-cell;
							padding:40px 30px 0 25px;
							vertical-align: top;
							strong{
								font-size: 18px;
