@import "../_component.scss";

.main-wrap{
	.section{
		h2{
			position: absolute;
			left: 50%;
			color: #fff;
			text-align: center;
			line-height: 1.5;

		}
	}

	.section-visual{
		position: relative;
		.slide{
			overflow: hidden;
			&:before{
				background-color: rgba(0,0,0,.2);
			}
			& > div{
				height: 100%;
				background-position: 50% 50%;
				background-size: cover;
			}
		}
		.slick-dots {
			white-space: nowrap;
		}
		h2{
			top: 50%;
			transform: translate(-50%, -50%);
			font-size: 30px;
			letter-spacing: -.75px;
			text-shadow: 0px 0px 60px rgba(0, 0, 0, 0.66);
			strong{
				font-size: 62px;
			}
		}
		img{
			max-width: none;
			object-fit: cover;
			object-position: 50% 50%;
		}
		video {
			position: absolute;
			top: 50%;
			left: 0;
			width: 100%;
			transform: translateY(-50%);
		}
		@supports(object-fit: cover) {
			video {
				top: 0;
				height: 100%;
				object-fit: cover;
				transform: none;
			}
		}
	}

	.section-activity{
		position: relative;
		overflow: hidden;
		height: 791px;
		background-color: #ededed;
		.slide{
			h2{
				top: 110px;
				transform: translateX(-50%);
				font-size: 48px;
				letter-spacing: -.5px;
				color: #2a2a2a;
				time{
					display: block;
					padding-top: 15px;
					font-size: 16px;
				}
			}
			.content{
				padding: {
					top: 265px;
					right: 20px;
					left: 20px;
				}
			}

		}
		.slick-arrow{
			border-top: 3px solid #888;
			border-left: 3px solid #888;
		}

		.program{

			&-img{
				width: 416px;
				height: 394px;
				&:before{
					padding-bottom: 94.71%;
				}
			}
			&-info{
				height: 394px;
				padding-top: 20px;
				padding-left: 465px;
			}
			&-title{
				margin-bottom: 20px;
				font-size: 36px;
				strong{
					font-weight: 400;
					white-space: normal;
				}
			}
			&-desc{
				//max-height: 48px;
				//margin-bottom: 35px;
			}
			&-state{
				strong{
					display: block;
					font-weight: 700;
					&:after{
						display: none;
					}
				}
				span.period{
					font-size: 18px;
				}
			}
			&-list {
				position: relative;
				max-width: 1325px;
				margin: 0 auto;
				padding-right: 25px;
				&.double {
					@include clear;
					li{
						box-sizing: border-box;
						float: left;
						width: 50%;
						padding-left: 25px;
					}
					.program{
						&-img{
							width: 250px;
							height: 300px;
						}
						&-info{
							height: 300px;
							padding-top: 0;
							padding-left: 290px;
						}
						&-title{
							height: auto;
							margin-bottom: 20px;
							font-size: 30px;
							font-weight: 400;
							strong{
								height: 78px;
								font-weight: 400;
								word-break: break-all;
								//letter-spacing: -2px;
							}
						}
						&-desc{
							margin-bottom: 20px;
						}
					}
					.btn{
						bottom: 3px;
						right: 0;
						left: 290px;
						padding: 0;
						width: auto;
						max-width: 220px;
					}
				}
			}
			.btn{
				right: auto;
				bottom: 61px;
				left: 465px;
				width: 220px;
			}
		}

		.volunteer-activities{
			position: relative;
			max-width: 1325px;
			margin: 0 auto;
			padding-right: 25px;
			@include clear;
			li{
				position: relative;
				box-sizing: border-box;
				float: left;
				width: 33.3334%;
				padding-left: 25px;
				.title{
					position: absolute;
					width: 100%;
					color: #2a2a2a;
					letter-spacing: -.5px;
					&:before{
						content: '';
						position: absolute;
						display: inline-block;
						height: 0;
						//background-color: rgba(255, 255, 255, .3);
						border-bottom: 1px solid rgba(255, 255, 255, .3);
					}
					strong{
						display: block;
						margin-left: 100px;
						font-size: 24px;
						font-weight: 400;
					}
					.count{
						font-size: 48px;
					}
				}
				.photo-lg{
					img {
						width: 100%;
					}
				}
			}
			.volunteer{
				.title{
					text-align: right;
					top: 0;
					right: 0;
					padding-top: 37%;
					&:before{
						top: 25px;
						left: 25px;
						right: 0;
						padding-top: 35%;
					}
					strong{
						position: relative;
						display: inline-block;
						margin-left: 0;
						padding: {
							top: 10px;
							right: 50px;
							left: 20px;
						}
						text-align: right;
						font-weight: 400;
						background-color: #ededed;
					}
					.count{
						position: absolute;
						top: 211px;
						right: -9px;
						transform: rotate(90deg) translate(20px, 100%);
						transform-origin: 100% 100%;
					}
				}
				.photo-lg{
					margin-bottom: 65px;
				}
				.photo-sm{
					float: right;
					width: 142px;
					margin-right: 50px;
				}
			}
			.volunteering-time{
				.title{
					top: 295px;
					left: 0;
					text-align: right;
					strong{
						position: absolute;
						top: 60px;
						right: 0;
					}
				}
			}
			.beneficiary{
				padding-top: 133px;
				.title{
					top: 25px;
					&:before{
						right: 25px;
						bottom: 5px;
						left: 0;
					}
					> div {
						text-align: right;
						display: inline-block;
					}
					strong {
						position: relative;
						z-index: 1;
						margin-left: 0;
						padding-right: 20px;
					}
					.count{
						font-size: 60px;
						line-height: 1;
						position: relative;
						padding-right: 20px;
						background-color: #ededed;
					}
				}
				.photo-lg{
					margin-bottom: 19px;
				}
				.photo-sm{
					width: 210px;
				}
			}
		}

		.slide {
			&-controls {
				position: absolute;
				bottom: 45px;
				width: 100%;
				max-width: 1300px;
				margin: 0 auto;
				text-align: right;
			}
			&-paging {
				display: inline-block;
				color: #2a2a2a;
				font-size: 24px;
				font-weight: 400;
				vertical-align: bottom;
				strong{
					font-size: 49px;
					font-weight: 400;
				}
			}
			&-auto{
				display: inline-block;
				margin-bottom: 8px;
				margin-left: 6px;
				vertical-align: bottom;
				.btn-pause,
				.btn-play{
					position: static;
				}
			}
		}
	}

	.section-news{
		position: relative;
		padding: {
			top: 203px;
			right: 45px;
			bottom: 220px;
			left: 45px;
		}
		box-sizing: border-box;
		background-size: cover;
		background-position: 50% 50%;
		background-image: url('../images/main-news-bg.jpg');
		&:before{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			display: block;
			background-color: rgba(222, 222, 222, 0.9);
		}
		h2{
			top: 105px;
			transform: translateX(-50%);
			font-size: 48px;
			letter-spacing: -.5px;
			color: #000;
		}
		.news-list{
			@include clear;
			position: relative;
			max-width: 1300px;
			margin: 0 auto 0;
			&:before {
				content: '';
				display: block;
				padding-bottom: 67.69%;
			}
		}
		li{
			position: absolute;
			width: 32%;
		}
		.title{
			position: absolute;
			top: 7.47%;
			left: 8.65%;
			line-height: 1.2;
			color: #333;
			font-size: 30px;
			z-index: 1;
			p{
				padding-right: 20px;
				letter-spacing: -1px;
			}
		}
		.category{
			display: inline-block;
			min-width: 100px;
			height: 30px;
			margin-bottom: 20px;
			line-height: 30px;
			color: #fff;
			font-size: 14px;
			text-align: center;
			vertical-align: top;
			border-left: 4px solid #a5cf4f;
			background-color: #0061a0;
		}
		.thumb{
			position: relative;
			&:before{
				content: '';
				display: block;
				padding-bottom: 128.125%;
				background-color: #f2f2f2;
				background-image: url('../images/no-img.png');
				background-position: 50% 50%;
				background-repeat: no-repeat;
			}
			span{
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
			}
		}
		.news {
			&-1 {
				top: 0;
				left: 0;
			}
			&-2 {
				top: 0;
				left: 34%;
			}
			&-3 {
				top: 0;
				left: 68%;
			}
			&-4 {
				bottom: 0;
			}
			&-5 {
				bottom: 0;
				left: 34%;
			}
			&-6 {
				bottom: 0;
				left: 68%;
			}
			&-2,
			&-4,
			&-6{
				.title{
					top: 12.4%;
				}
				.thumb{
					&:before{
						padding-bottom: 77.64%;
					}
				}
			}
		}
		img {
			max-width: 100%;
		}
		li.theme-white{
			.title{
				color: #fff;
				p{
					text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
				}
			}
		}
	}

	.section-service{
		height: 212px;
		text-align: center;
		background-color: #5d5d5d;
		ul{
			display: flex;
			width: 100%;
			max-width: 1300px;
			height: 100%;
			margin: 0 auto;
			@include clear;
		}
		li{
			flex: 1 1 auto;
			height: 100%;
			text-align: center;
			color: #fff;
			font-size: 14px;
			line-height: 1.2;
			letter-spacing: -.5px;
			a{
				position: relative;
				display: block;
				height: 100%;
				&:hover{
					background-color: #3e3e3e;
				}
			}
			em{
				position: absolute;
				top: 63px;
				left: 0;
				display: block;
				width: 100%;
				height: 43px;
				line-height: 43px;
				margin-left: 0;
				text-align: center;
			}
			span{
				display: block;
				padding-top: 10px;
				line-height: 1.3;
			}
		}
	}
}

.main-wrap {
	&,
	& ~ .footer {
		//min-width: 1300px;
	}
}









