@import "./contents/_login.scss";
@import "./contents/_main.scss";
@import "./contents/_sharestory.scss";
@import "./contents/_business.scss";
@import "./contents/_mypage.scss";
@import "./contents/_program.scss";
@import "./contents/_introduce.scss";
@import "./contents/_error.scss";


.btn-gotop{
	position: absolute;
	right: 20px;
	bottom: 0;
	width: 0;
	height: 0;
	opacity: 0;
	transition: opacity 0.5s;
	background-color: rgba(255, 255, 255, 0.9);
}
.scroll-top .btn-gotop{
	z-index: 1;
	opacity: 1;
}

.section-contents{
	width: 100%;
	max-width: 880px;
	margin: 0 auto;
}

.infobox{
	padding: 55px 30px 35px;
	border-bottom: 1px solid #ccc;
	background-color: #f3f2f3;
	p{
		margin-bottom: 20px;
	}
}

.rejectbox{
	padding: 25px 30px;
	border-bottom: 1px solid #ccc;
	line-height: 1.8;
	font-size: 14px;
	background-color: #f9f9f9;
}
.group{
	position: relative;
	margin-bottom: 60px;
	&-line{
		padding-bottom: 30px;
		border-bottom: 1px solid #ccc;
	}
}

.search-wrap{
	text-align: center;
	margin: 40px 0;
	.selectbox{
		display: inline-block;
		width: 130px;
		vertical-align: middle;
	}
	.input-box {
		width: auto;
		input {
			width: 220px;
		}
	}
}

.confirm-wrap{
	margin-bottom: 70px;
	.confirm-message{
		padding-top: 95px;
		padding-bottom: 65px;
		border-top: 1px solid #000;
		border-bottom: 1px solid #ccc;
		font-size: 36px;
		color: #333;
		line-height: 1.3;
		text-align: center;
		p{
			margin-bottom: 35px;
			letter-spacing: -.5px;
		}
		strong{
			font-weight: 700;
		}
		small{
			display: inline-block;
			font-size: 16px;
			color: #888;
			line-height: 1.5;
		}
		.primary {
			color: #0074b2;
		}
	}
	.btn{
		width: 160px;
		margin: 0 8px;
		padding: 0;
	}
}

.section-board{
	margin: -40px 0 0;
	padding: 40px 0 100px;
	background-color: #dcdcdc;
}

//------------------------------------------
.budget-content {
	.table {
		&-vertical {
			//margin-top: 40px;
			//padding-bottom: 25px;
			border-bottom: 1px solid #ccc;
			> ul {
				table-layout: fixed;
				> li {
					&:first-child {
						> * {
							&:before {
								color: #000;
								font: {
									weight: 700;
								}
							}
						}
					}
					> * {
						padding: {
							top: 10px;
							right: 10px;
							bottom: 10px;
							left: 10px;
						}
						border-bottom: 0;
						text-align: center;
						&:first-child {
							padding: {
								right: 10px;
								left: 0;
							}
						}
						&:last-child {
							padding: {
								right: 0;
								left: 10px;
							}
						}
					}
				}
			}
			.tfoot {
				&:before {
					content: '';
					display: table-cell;
				}
				> div {
					color: #000;
					font: {
						weight: 700;
					}
					text-align: right;
				}
			}
		}
	}
	.input-group {
		> * {
			display: table-cell;
		}
	}
}

