@import "../_component.scss";

.section-login{
	padding-top: 90px;
	padding-bottom: 186px;
	background-color: #dcdcdc;
	& > .inner{
		width: 880px;
		margin: 0 auto;
		@include clear;
	}
	.bg{
		width: 267px;
		height: 267px;
		margin: 0 auto 37px;
		&-employees{
			background-image: url('../images/login-employees.png');
		}
		&-agents{
			background-image: url('../images/login-agents.png');
		}
		&-search-pw{
			width: 229px;
			height: 229px;
			background-image: url('../images/search-pw.png');
		}
	}

	&.login-gate{
		padding-top: 40px;
		.login-text{
			margin-bottom: 25px;
			text-align: center;
			color: #333;
			font-size: 16px;
			letter-spacing: -.5px;
		}
		.login-type{
			position: relative;
			float: left;
			width: 426px;
			padding: 53px 0 71px;
			text-align: center;
			background-color: #fff;
			/*
			&.type-employees{
				margin-right: 23px;
			}
			*/
			&.type-agents{
				margin-left: 28px;
			}
			/*
			&:after{
				content: '';
				position: absolute;
				top: 5%;
				left: -23px;
				display: inline-block;
				width: 1px;
				height: 90%;
				border-left: 1px dashed #eee;
			}
			*/
			&:first-child:after{
				display: none;
			}
			.btn{
				width: 241px;
				height: 60px;
				font-size: 18px;
				font-weight: 300;
			}
		}
	}
	.login-wrap{
		@include clear;
		position: relative;
		padding: 80px 0 90px;
		padding-bottom: 44px;
		background-color: #fff;
		.bg{
			float: left;
			margin: {
				top: 30px;
				left: 84px;
				right: 85px;
			};
		}
		.title{
			display: inline-block;
			margin-bottom: 25px;
			color: #000;
			font-size: 30px;
			font-weight: 700;
		}
	}
	.login-box{
		float: left;
		.login-form{
			@include clear;
			position: relative;
			margin-bottom: 25px;
		}
		.login-input{
			float: left;
			margin-right: 20px;
			.input-box{
				display: block;
				margin-bottom: 10px;
				input{
					width: 210px;
				}
			}
		}
		.id-save{
			position: absolute;
			top: 8px;
			left: 230px;
			font-size: 14px;
		}
		.btn-login{
			float: left;
			width: 145px;
			height: 40px;
			margin-top: 50px;
			font-size: 18px;
		}
		.btn-link{
			padding-left: 0;
			margin-right: 30px;
			font-size: 16px;
			font-weight: 400;
		}
		.login-info{
			margin-top: -10px;
			margin-bottom: 20px;
			line-height: 1.7;
			font-size: 14px;
			color: #000;
		}
		.login-help{
			margin-top: 10px;
			padding: 13px 15px 13px 30px;
			font-size: 14px;
			color: #888;
			line-height: 1.4;
			background-color: #f4f4f4;
			p:before{
				content: '';
				margin-right: 3px;
				margin-left: -15px;
			}
		}
	}

	.search-pw-box{
		.login-form{
			margin-bottom: 30px;
		}
		.login-input{
			margin-right: 0;
			.input-box{
				input{
					width: 280px;
				}
			}
		}
		label{
			float: left;
			display: inline-block;
			width: 100px;
			height: 40px;
			font-size: 14px;
			line-height: 40px;
			color: #000;
			font-weight: 700;
		}
		.btn{
			margin-left: 100px;
		}
	}
}

.section-join{
	& > .inner{
		width: 880px;
		margin: 0 auto;
		@include clear;
	}
	.join-steps{
		ol {
			position: relative;
			display: table;
			table-layout: fixed;
			width: 100%;
			height: 65px;
			margin-top: 85px;
			margin-bottom: 55px;
		}
		li{
			position: relative;
			display: table-cell;
			&:before{
				content: '';
				position: absolute;
				top: 16px;
				left: 0;
				right: 0;
				height: 2px;
				background-color: #ccc;
			}
			&.finish,
			&.active {
				&:before {
					background-color: #0074b2;
				}
			}
			div{
				position: absolute;
				top: 0;
				right: 0;
				z-index: 1;
				text-align: center;
				transform: translateX(50%);
			}
			&:first-child{
				width: 0;
				div {
					left: 0;
					right: auto;
					transform: translateX(0%);
				}
				& + li {
					&:before {
						left: 42px;
					}
				}
			}
			&:last-child {
				&:before {
					left: 0;
					right: 42px;
				}
				div {
					transform: translateX(0%);
				}
			}
			.step-number{
				position: relative;
				display: inline-block;
				width: 34px;
				height: 34px;
				margin-bottom: 5px;
				color: #ccc;
				font-size: 20px;
				font-weight: 700;
				text-align: center;
				border: 3px solid #ccc;
				border-radius: 50%;
				box-sizing: border-box;
				background-color: #fff;
			}
			span{
				display: block;
				color: #ccc;
				font-size: 16px;
				font-weight: 700;
			}
			&.active{
				.step-number{
					color: #0074b2;
					border-color: #0074b2;
				}
				span{
					color: #0074b2;
				}
			}
			&.finish{
				.step-number{
					color: #fff;
					border-color: #0074b2;
					background-color: #0074b2;
				}
				span{
					color: #0074b2;
				}
			}
		}
	}
	.join-info-text{
		margin-bottom: 0;
		padding-bottom: 10px;
		font-size: 14px;
		line-height: 1.7;
		color: #717171;
		letter-spacing: -.75px;
		//border-bottom: 1px solid #ccc;
		.require{
			float: none;
			margin-right: 0;
			margin-bottom: 20px;
			vertical-align: middle;
		}
	}
	.group-terms{
		position: relative;
		margin-bottom: 80px;
		.contents{
			height: 170px;
			margin-top: 18px;
			padding: 20px;
			border: 1px solid #ccc;
			box-sizing: border-box;
			overflow-y: scroll;
		}
		.section-terms{
			& > .inner{
				padding-top: 0;
				padding-bottom: 0;
			}
			h4{
				padding-top: 0;
			}
		}
		.term-agree{
			position: absolute;
			top: 18px;
			right: 0;
			.checkbox{
				margin-right: 0;
			}
			label{
				color: #000;
				font-size: 14px;
				&:before{
					border-color: #000;
				}
			}
		}
	}
	.table{
		.btn{
			padding: 0 20px;
		}
	}
	.confirm-wrap{
		margin-top: 85px;
	}
}

.section-terms{
	& > .inner{
		max-width: 880px;
		margin: 0 auto;
		font-size: 14px;
		padding: {
			top: 30px;
			bottom: 50px;
		}
		letter-spacing: -1px;
		line-height: 1.5;

		> ol, ul {
			> li {
				margin-bottom: 10px;
				> ol, ul {
					margin-top: 10px;
					padding-left: 18px;

					span {
						display: block;
						padding-left: 15px;
					}
				}
			}
			+ p {
				margin-top: 20px;
			}
		}
	}
	h3 {
		font: {
			size: 16px;
			weight: bold;
		}
		margin-bottom: 30px;

		& + p {
			margin: {
				top: -10px;
				bottom: 60px;
			}
		}
		+ h4 {
			padding: {
				top: 20px;
				bottom: 0;
			}
		}
	}
	h4 {
		padding: {
			top: 60px;
			left: 0;
			bottom: 0;
		}
		font-size: 14px;
	}
	strong {
		display: block;
		margin: {
			top: 30px;
			bottom: 20px;
		}
		& + p, & + ol {
			margin: {
				bottom: 20px;
			}
		}
	}
	p {
		margin-top: 5px;
	}
	em {
		font-weight: 600;
	}
	.supplementary {
		margin-top: 30px;
		font: {
			weight: bold;
		}
		span {
			display: block;
		}
	}
}