@import "../_component.scss";


.section-result{
	padding-top: 95px;
	h2{
		font-size: 48px;
		text-align: center;
		strong{
			font-weight: 700;
		}
		time{
			display: block;
			margin-top: 20px;
			font-size: 16px;
		}
	}
	.content{
		background-image: url('../images/bg-result.png');
		background-position: 50% 0;
		background-repeat: no-repeat;
		ul{
			position: relative;
			width: 880px;
			margin: 45px auto 0;
		}
		li{
			position: relative;
			width: 440px;
			.title{
				position: absolute;
				.line{
					display: inline-block;
					height: 4px;
					background-color: #000;

				}
				strong{
					font-weight: 400;
					font-size: 24px;
				}
				.count{
					display: block;
					font-weight: 400;
					font-size: 48px;
				}
			}
		}
		.volunteering-time{
			margin-bottom: 30px;
			text-align: right;
			.title{
				bottom: 30px;
				right: 300px;
			}
			.count{
				margin-top: -93px;
			}
			.photo{
				display: inline-block;
				width: 270px;
				margin-right: 20px;
			}
		}
		.volunteer{
			text-align: right;
			.title{
				left: 0;
				right: 20px;
				top: 150px;
				padding-left: 10px;
				.line{
					width: 100%;
					margin-top: 35px;
					vertical-align: top;
				}
				strong{
					position: relative;
					display: inline-block;
					top: -17px;
					padding-left: 20px;
					padding-right: 50px;
					background: #fff;
				}
			}
			.count{
				position: absolute;
				top: -50%;
				right: -15px;
				transform: rotate(90deg) translate(100%, 100%);
				transform-origin: 100% 100%;
			}
			.photo{
				display: inline-block;
				margin-right: 20px;
				&-1{
					width: 412px;
					margin-bottom: 70px;
				}
				&-2{
					width: 142px;
					margin-right: 70px;
				}
			}

		}
		.partner{
			position: absolute;
			top: 229px;
			right: 0;
			.title{
				left: 310px;
				.count{
					line-height: 1.1;
				}
			}
			.photo{
				display: inline-block;
				&-1{
					width: 300px;
				}
			}
		}
		.beneficiary{
			position: absolute;
			top: 448px;
			right: 0;
			.title{
				right: 18px;
				bottom: 10px;
				color: #fff;
				text-align: right;
			}
			.photo{
				display: inline-block;
				&-1{
					& > div{
						height: 250px;
						img{
							height: 100%;
						}
					}
				}
			}
		}
		@media (min-width:961px) {

		}

	}

	.result {
		&-biz {
			.slide {
				height: auto;
				h3 {
					font: {
						size: 36px;
					};
					text-align: center;
				}
			}
		}
		&-nav {
			position: relative;
			.slick {
				&-arrow {
					color: #ccc;
					font: {
						size: 24px;
						weight: 700;
					};
					line-height: normal;
					top: 10px;
					width: auto;
					height: auto;
					border: 0;
					transform: none;
					&:after {
						content: '';
						position: absolute;
						top: 50%;
						box-sizing: border-box;
						display: block;
						width: 12px;
						height: 12px;
					}
				}
				&-prev {
					left: 0;
					padding-left: 25px;
					&:after {
						left: 0;
						border-right: 3px solid;
						border-bottom: 3px solid;
						transform: rotate(135deg) translateX(-50%);
					}
				}
				&-next {
					right: 0;
					padding-right: 25px;
					&:after {
						right: 0;
						margin-right: 6px;
						border-right: 3px solid;
						border-bottom: 3px solid;
						transform: rotate(315deg) translateX(50%);
					}
				}
			}
		}
		li {
			float: left;
		}
	}
}