@import "../_component.scss";

.description{
	max-width: 880px;
	margin: 0 auto 35px;
	font-weight: 700;
	color: #333;
	line-height: 1.5;
}

.program{
	position: relative;
	max-width: 880px;
	margin: 0 auto;
	&-img{
		position: absolute;
		top: 0;
		left: 0;
		width: 250px;
		height: 300px;
		text-align: center;
		white-space: nowrap;
		background-color: #f2f2f2;
		background-image: url('../images/no-img.png');
		background-position: 50% 50%;
		background-repeat: no-repeat;
		overflow: hidden;
		&:after{
			content: '';
			display: inline-block;
			height: 100%;
			vertical-align: middle;
		}
		span{
			display: block;
			height: 100%;
			vertical-align: middle;
		}
		img{
			width: 100%;
			height: 100%;
			max-width: 100%;
			max-height: 100%;
			margin: 0 auto;
			object-fit: cover;
			vertical-align: middle;
		}
	}
	&-info{
		height: 300px;
		padding-top: 25px;
		padding-left: 290px;
		color: #2a2a2a;
		box-sizing: border-box;
	}
	&-category{
		padding: 50px 0 20px;
		color: #2a2a2a;
		font-size: 18px;
	}
	&-title{
		margin-bottom: 30px;
		font-size: 30px;
		line-height: 1.3;
		.category{
			display: inline-block;
			min-width: 80px;
			height: 30px;
			margin-bottom: 5px;
			//margin-left: 5px;
			line-height: 30px;
			color: #fff;
			font-size: 14px;
			text-align: center;
			vertical-align: top;
			border-right: 7px solid #a5cf4f;
			background-color: #575757;
		}
		strong{
			display: block;
			font-weight: 400;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			word-break: break-all;
		}
	}
	&-desc{
		width: 100%;
		height: 47px;
		display: block;
		display: -webkit-box;
		margin-bottom: 20px;
		word-break: break-all;
		font-size: 18px;
		line-height: 1.3;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	&-state{
		strong{
			display: inline-block;
			width: 75px;
			font-weight: 400;
			&:after{
				content: ':';
				float: right;
			}
		}
	}
	.btn{
		position: absolute;
		bottom: 0;
		right: 0;
		width: 131px;
	}
}

.program-list{
	margin-bottom: 30px;
	.group{
		position: relative;
		padding: 30px;
		margin-bottom: 0;
		border-bottom: 1px solid #ccc;
		&:first-child{
			border-top: 1px solid #000;
		}
	}
	.program{
		&-info{
			padding-top: 30px;
			padding-bottom: 15px;
		}
		&-title{
			margin-bottom: 20px;
			strong{
				display: block;
				//height: 94px;
				//display: -webkit-box;
				//overflow: hidden;
				//white-space: normal;
				//text-overflow: ellipsis;
				//-webkit-box-orient: vertical;
				//-webkit-line-clamp: 2;
			}
		}
		&-desc{
			margin-bottom: 20px;
		}
		&-state{
			font-size: 18px;
			strong{
				display: block;
				font-weight: 700;
				&:after{
					display: none;
				}
			}
		}
		.btn{
			left: 290px;
			width: 130px;
		}
	}
}

.program-wrap{
	padding: 30px 0;
	background-color: #f2f2f2;
	.program{
		max-width: 880px;
		margin: 0 auto;
	}
}

.box{
	margin: 65px 0 50px;
	& > .inner{
		position: relative;
		max-width: 880px;
		margin: 0 auto;
	}
	h3{
		margin-bottom: 25px;
		font-weight: 700;
		font-size: 24px;
		color: #333;
	}
	h4 {
		margin-bottom: 25px;
		padding: 0;
		font-weight: 700;
		font-size: 20px;
		color: #333;
	}
	.con{
		margin-bottom: 35px;
		line-height: 1.5;
		color: #333;
		letter-spacing: -1px;
	}
	.btns{
		text-align: right;
	}
}

.finance{
	background-color: #fcfcfc;
	& > .inner{
		position: relative;
		max-width: 880px;
		margin: 0 auto;
	}
	.item{
		position: relative;
		margin: 55px 0 50px;
	}
	h4{
		padding: 0;
		color: #1f8bcc;
		font-weight: 700;
		font-size: 18px;
	}
	.sub-title{
		padding-top: 15px;
	}
	.elements{
		padding-top: 30px;
		li{
			margin-bottom: 15px;
			color: #333;
			strong{
				position: relative;
				display: inline-block;
				width: 95px;
				padding-left: 23px;
				font-weight: 700;
				box-sizing: border-box;
			}
			.flag{
				position: absolute;
				top: 3px;
				left: 0;
				display: inline-block;
				border: 8px solid transparent;
				&-1{
					border-color: #a9c33e transparent transparent #a9c33e;
				}
				&-2{
					border-color: #1f8bcc transparent transparent #1f8bcc;
				}
				&-3{
					border-color: #005fa1 transparent transparent #005fa1;
				}
			}
		}
	}
	.img-financial-health {
		position: absolute;
		right: 0;
		top: 50%;
		margin-top: -100px;
	}
	.standards{
		@include clear;
		margin-top: 45px;
		li{
			float: left;
			width: 182px;
			color: #333;
			&:last-child{
				width: 149px;
			}
			.icon{
				display: block;
				width: 148px;
				height: 148px;
				border-radius: 50%;
			}
			strong{
				display: block;
				padding-top: 25px;
				padding-left: 30px;
			}
			span{
				display: block;
				padding-top: 10px;
				padding-left: 30px;
				line-height: 1.3;
			}

		}
	}
}



