@import "../_component.scss";

.vision {
	color: #333;
	text-align: center;
	.headline {
		padding: 70px 0 0;
		line-height: 1.8;
		font-size: 20px;
	}
	h3 {
		display: inline-block;
		margin-bottom: 15px;
		padding: 10px 60px;
		text-align: center;
		border-radius: 36px;
		border: 3px solid #0061a0;
		box-sizing: border-box;
		letter-spacing: 5px;
		font-size: 36px;
	}
	h4 {
		display: inline-block;
		width: 280px;
		height: 54px;
		margin-bottom: 20px;
		padding: 10px 0 0;
		text-align: center;
		border-radius: 36px;
		border: 3px solid transparent;
		box-sizing: border-box;
		font-size: 20px;
		font-weight: 700;
		color: #000;
		letter-spacing: 2px;
		text-transform: uppercase;

	}
	.box {
		@include clear;
		&-mission{
			position: relative;
			margin-top: 75px;
			margin-bottom: 95px;
			font-size: 24px;
			font-weight: 300;
			&:after{
				position: absolute;
				left: 50%;
				bottom: -145px;
				width: 1px;
				height: 100px;
				margin-left: -0.5px;
				background-color: #000;
			}
		}
		&-strategic{
			h4{
				border-color: #d0d0d0;
				background-color: #fff;
			}
			.items{
				@include clear;
				margin: 0 -15px;
			}
			.item{
				position: relative;
				float: left;
				width: 207px;
				height: 258px;
				margin: 0 10px;
				font-size: 20px;
				box-shadow: 8px 10px 25px 0px #e5e5e5;
				&:after {
					content: '';
					position: absolute;
					top: 100%;
					left: 50%;
					display: inline-block;
					width: 0px;
					height: 100%;
					margin-left: -1px;
					border-left: 3px dashed;
				}
				&-financial:after {
					border-color: #58c1f7;
				}
				&-healthy:after {
					border-color: #a4ce4e;
				}
				&-culture:after {
					border-color: #0061a0;
				}
				&-envirement:after {
					border-color: #00aca0;
				}
			}
		}
		&-activities{
			position: relative;
			margin-bottom: 0;
			/*
			&:before,
			&:after{
				content: '';
				position: absolute;
				top: -60px;
				display: block;
				width: 2px;
				height: 145px;
				background-color: #ccc;
			}
			&:before{
				left: 205px;
			}
			&:after{
				right: 205px;
			}
			*/
			h4{
				margin-bottom: 0;
				border-color: #d0d0d0;
				background-color: #fff;
			}
			.items{
				margin: -30px 0 0;
				padding: 55px 10px 45px;
				border: 5px solid #f4f4f4;
				box-sizing: border-box;
				box-shadow: 5px 5px 20px 0px #e5e5e5;
				background-color: #fff;
				@include clear;
			}
			.item{
				float: left;
				width: 25%;
				border-top: none;
			}
		}
	}
	.box-group{
		padding-top: 79px;
		padding-bottom: 69px;
		background-color: #fafafa;
		background-image: url('../images/bg-vision.png');
		background-repeat: no-repeat;
		background-position: 50% 280px;
		.box{
			max-width: 880px;
			margin: 0 auto 60px;
		}
		.box-activities{
			margin-bottom: 0;
		}
	}
	.item {
		border-top: 6px solid transparent;
		background-color: #fff;
		box-sizing: border-box;
		font-size: 24px;
		> span {
			text-align: center;
			white-space: nowrap;
			display: block;
			height: 105px;
			margin-bottom: 15px;
			&:after {
				content: '';
				vertical-align: bottom;
				display: inline-block;
				height: 100%;
			}
		}
		.icon {
			vertical-align: bottom;
		}
		.title {
			display: block;
			margin-bottom: 8px;
			font-weight: 700;
			font-size: 24px;
			line-height: 1.2;
		}
		ul {
			padding: 0 18px;
		}
		li {
			position: relative;
			margin-bottom: 10px;
			padding-left: 15px;
			font-size: 16px;
			font-weight: normal;
			text-align: left;
			letter-spacing: -1px;
			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				left: 0;
				top: 8px;
				width: 4px;
				height: 4px;
				border-radius: 50%;
			}
		}
		&-financial{
			border-color: #58c1f7;
			li{
				&:before {
					background-color: #58c1f7;
				}
			}
		}
		&-healthy{
			border-color: #a4ce4e;
			li{
				&:before {
					background-color: #a4ce4e;
				}
			}
		}
		&-culture{
			border-color: #0061a0;
			.title{
				padding-top: 15px;
			}
			li{
				&:before {
					background-color: #0061a0;
				}
			}
		}
		&-envirement{
			border-color: #00aca0;
			.title{
				padding-top: 15px;
			}
			li{
				&:before {
					background-color: #00aca0;
				}
			}
		}
	}
}

.greeting {
	color: #333;
	.headline {
		padding: 70px 0;
		font-size: 36px;
		line-height: 1.3;
		text-align: center;
	}
	.contents {
		padding: 65px 0 40px;
		line-height: 1.5;
		font-size: 16px;
		p {
			margin: 10px 0;
			letter-spacing: -1px;
			word-break: break-all;
			/*
			&:first-child {
				position: relative;
				text-align: right;
				&:before {
					content: '';
					position: absolute;
					left: 0;
					top: 50%;
					display: inline-block;
					width: 100%;
					border-top: 1px solid #ccc;
				}
				span {
					position: relative;
					z-index: 1;
					display: inline-block;
					padding-left: 10px;
					background-color: #fff;
				}
			}
			*/
		}
	}
	.img {
		width: 700px;
		margin: 0 auto 100px;
		text-align: center;
	}
}

.director {
	color: #333;
	padding: 45px 0 110px;
	.item {
		@include clear;
		padding: 35px 0;
		.img {
			position: relative;
			float: left;
			width: 250px;
			height: 187px;
			overflow: hidden;
			img{
				position: absolute;
				top: 50%;
				left: 50%;
				max-width: none;
				height: 100%;
				transform: translate(-50%, -50%);
			}
		}
		.contents {
			float: left;
			width: 630px;
			padding-left: 30px;
			box-sizing: border-box;
			strong,
			span {
				display: block;
				font-size: 20px;
			}
			p {
				margin-top: 10px;
				letter-spacing: -1px;
				line-height: 1.5;
				word-break: break-all;
			}
		}
	}
}
.history {
	padding-bottom: 100px;
}
.list {
	&-history {
		> li {
			@include clear;
			position: relative;
			padding: 70px 0;
			border-bottom: 1px solid #ccc;
			&:first-child{
				padding-top: 0;
			}
			&:before {
				display: none;
				content: attr(data-year)"";
				position: absolute;
				right: 0;
				bottom: 0;
				font-size: 150px;
				font-weight: bold;
				letter-spacing: -15px;
				color: rgba(0,0,0,0.08);
			}
			.img {
				position: relative;
				float: left;
				width: 294px;
				height: 180px;
				box-sizing: border-box;
				overflow: hidden;
				img{
					position: absolute;
					top: 50%;
					left: 50%;
					max-width: none;
					height: 100%;
					transform: translate(-50%, -50%);
				}
			}
			.contents {
				float: left;
				width: 586px;
				padding-left: 40px;
				box-sizing: border-box;
				strong{
					display: block;
					margin-top: -6px;margin-bottom: 10px;
					line-height: 1;
					font-size: 48px;
					color: #b0b0b0;
					letter-spacing: -4px;
				}
				ul {
					li {
						position: relative;
						display: block;
						padding-left: 10px;
						margin: 10px 0;
						color: #333;
						letter-spacing: -.75px;
						&:before {
							content: '-';
							position: absolute;
							left: 0;
						}
					}
				}
			}
		}
	}
}


.contribution{
	padding-bottom: 100px;
	.table{
		table{
			width: 100%;
			margin: 55px 0;
		}
		thead{
			th{
				padding: 15px 0;
				border-bottom: 1px solid #000;
				text-align: left;
				color: #333;
				strong{
					padding-left: 20px;
					font-size: 24px;
					font-weight: 400;
				}
				span{
					float: right;
					margin-top: 5px;
					font-weight: 700;
					font-size: 16px;
				}
			}
		}
		tbody{
			td{
				padding: 20px;
				border-top: 1px solid #ccc;
				text-align: left;
				color: #333;
				.summary{
					@include clear;
					strong{
						font-size: 16px;
						font-weight: 700;

					}
					span{
						float: right;
						font-size: 16px;
						font-weight: 400;
					}
				}
				ul{
					margin-top: 10px;
					li{
						@include clear;
						line-height: 1.5;
						strong{
							font-size: 16px;
							font-weight: 400;
						}
						span{
							float: right;
							font-size: 16px;
							font-weight: 400;
						}
					}
				}
			}
			tr:first-child{
				border-top: none;
			}
		}
		tfoot{
			td{
				padding: 25px 20px 25px 0;
				color: #333;
				border-top: 1px solid #000;
				border-bottom: 1px solid #ccc;
				background-color: #fdfdfd;
				text-align: left;
				strong{
					padding-left: 50px;
					font-size: 16px;
					font-weight: 700;
				}
				span{
					float: right;
					font-weight: 700;
					font-size: 16px;
				}
			}
		}
	}
	.link-to{
		text-align: center;
		color: #0074b2;
	}
}

.contact {
	padding-bottom: 90px;
	text-align: center;
	line-height: 1.625;
	.wrap-map {
		position: relative;
		max-width: 880px;
		margin: 65px auto 0;
		background-color: #f7f7f7;
		&:before{
			content: '';
			display: block;
			padding-bottom: 42.86%;
		}
		.map {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			border: 1px solid #ccc;
			#map{
				z-index: 1;
				height: 100%;
			}
		}
	}
	.location-info{
		max-width: 880px;
		margin: 0 auto;
		text-align: left;
		ul{
			padding-top: 20px;
			padding-left: 10px;
		}
		li{
			margin: 15px 0;
			*{
				display: inline-block;
				vertical-align: middle;
				color: #333;
			}
			em{
				min-width: 95px;
				font-weight: 700;
			}
		}
	}
}

.groups {
	color: #333;
	.headline {
		color: #2a2a2a;
		text-align: center;
		margin-top: 100px;
		span {
			display: block;
			font-size: 48px;
		}
		b {
			display: block;
			font-size: 48px;
		}
		.img {
			width: 792px;
			margin: 45px auto 0;
		}
		.text {
			max-width: 792px;
			margin: 20px auto 0;
			text-align: center;
			font-size: 20px;
			line-height: 1.5;
			font-weight: bold;
			color: #105fa0;
		}
	}
	.box-wrap {
		margin-top: 70px;
		> div {
			padding-top: 70px;
			border-top: 1px solid #ccc;
		}
		.box-top {
			strong {
				padding-left: 17px;
				position: relative;
				display: block;
				color: #007abc;
				font-size: 24px;
				line-height: 1;
				&:before {
					display: block;
					content: "";
					width: 12px;
					height: 12px;
					position: absolute;
					top:50%;
					left:0;
					margin-top:-6px;
					background-color: #007abc;
					border-radius: 100px;
				}
				&:after {
					display: block;
					content: "";
					width: 6px;
					height: 6px;
					position: absolute;
					top:50%;
					left:3px;
					margin-top:-3px;
					background-color: #fff;
					border-radius: 100px;
				}
			}
			.text {
				margin-top: 25px;
				font-size: 16px;
				line-height: 1.5;
			}
		}
		.box-cont {
			margin-top:60px;
			strong {
				font-size: 18px;
			}
			ul {
				@include clear;
			}
			li {
				float: left;
				width: 277px;
				height: 225px;
				background-color: #fff;
				margin-left: 24px;
				margin-top: 24px;
				border-bottom:3px solid #ccc;
				&.col1, &.col4 {
					margin-left: 0px;
				}
				.numbers {
					margin-top: 10px;
					strong {
						font-size: 75px;
						position: relative;
					}
					span {
						color:#878787;
						font-weight: bold;
						font-size: 18px;
						position: relative;
    					left: -5px;
					}
					&.plus strong:after {
						display: block;
						content: "+";
						position: absolute;
						top: -2px;
						right: -25px;
						font-size: 43px;
					}
				}
				&.col1 {
					.numbers strong {
						color: #0090da;
					}
				}
				&.col2 {
					.numbers strong {
						color: #1d64a4;
					}
				}
				&.col3, &.col5 {
					.numbers strong {
						color: #965bbf;
					}
				}
				&.col4 {
					.numbers strong {
						color: #b4d76e;
					}
				}
				&.col6 {
					.numbers strong {
						color: #0db1a8;
					}
				}

				> p {
					font-weight: bold;
					font-size: 18px;
				}
				> em {
					display: block;
					margin-top: 5px;
					font-size: 16px;
				}

			}
		}



		+ .box-wrap {
			background-color: #f7f7f7;
			> div {
				border:0;
				padding-bottom: 70px;
			}

			.box-cont {
				li{
					border-bottom:0;
					padding-left: 40px;
					box-sizing: border-box;
					-webkit-box-shadow: 8px 10px 25px 0px #e5e5e5;
					box-shadow: 8px 10px 25px 0px #e5e5e5;
					.numbers {
						margin-top: 20px;
					}
				}
			}
		}
	}
}

.icon-groups {
    background-repeat: no-repeat;
    background-image: url("../images/icons/pc/groups.png");
    width: 42px;
    height: 52px;
}
a:hover .icon-groups {
    background-image: url("../images/icons/pc/groups-hover.png");
}
.link-to-metlife{
	margin-left: 20px;
}

@media (max-width:$medium-devices) {
	.link-to-metlife{
		margin-top: 20px;
		margin-left: 0;
	}
}